@import "~bootstrap/scss/bootstrap-grid";

.services__path {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 30px 0;

  a {
    font-size: 12px;
    color: rgb(33, 33, 33);
    line-height: 2.333;
    text-decoration: none;

    &:after {
      content: "|";
      display: inline-block;
      margin: 0 13px;
    }

    &:last-child {
      font-weight: bold;

      &:after {
        display: none;
      }
    }
  }
}



.services {
  font-family: 'Open Sans', sans-serif;
  color: #212121;
  margin-top: 120px;
  

  &__wrapperright {
    padding: 83px 23px 14px 91px;
  }
  &__wrapperleft {
  
    padding: 110px 30px 30px 72px;
  }
  &__crumbs {
    display: flex;
    padding: 0;
    margin: 0 0 0 -14px;

    .services__crumb:last-child a {
      border-right: none;
    }
  }

  &__block {
    // padding: 0 17px;
    display: flex;
    margin: 17px 0;
  }

  &__crumb {
    display: block;

    a {
      font-size: 12px;
      color: #212121;
      display: block;
      padding: 0 14px;
      border-right: 1px solid #212121;
      text-decoration: none;
    }

  }

  &__title {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 40px;
    color: #184494;
    margin: 0 0 32px 0;
  }

  &__wrapper {
    width: 50%;
    display: flex;
    flex-direction: column;  
      
  }
  &__image {
    max-width: 100%;
    border-radius: 6px;
  }

  &__subtitle {
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 18px;
    color: #212121;
  }

  &__text {
    font-size: 14px;
    margin-top: 8px;
    line-height: 24px;
  }
  &__inr-btn{
    flex-grow: 1;    
    align-items: flex-end;
    justify-content: flex-end;
    display: flex;
  }
  &__btn {
    width: 191px;
    min-height: 44px;
    padding: 5px 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #009fe3;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 14px;
    border: none;
    transition: .3s;
    margin: 0 auto 45px;
    border-radius:3px;
    text-decoration: none;
    &:hover{
      background: #0084bd;
    }
  }
}

.crumb-active {
  font-weight: 700;
}

@media (max-width:1200px){
  .services__wrapperright {
    padding: 21px 23px 14px 40px;
  }
  .services__wrapperleft {
    padding: 30px 30px 30px 40px;
  }  
}

@media(max-width:992px){
  .services__block{
    flex-direction: column;
  }
  .services__wrapper{
    width: 100%;
  }
  .services__wrapperright, .services__wrapperleft{
    padding: 0;
  }
  .services__wrapperright{
    order: -1;
  }
  .services__wrapper{
    margin-top: 17px;   
  }
  .services__inr-btn{
    justify-content: flex-start;
  }
  .services__btn{
    margin: 5px 0 5px 0;
  }
  .services__title{
    margin: 14px 0 0 0 ;
  }
  .services__subtitle{
    margin: 0;
  }
  .services__block{
    margin: 40px 0;
  }
  .services__block-first{
    margin: 10px 0 40px 0;
  }

  
}

.pop-call {
  position: fixed;
  max-width: 500px;
  margin: 0 auto;
  background: #Fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pop {
  &__block {
    display: flex;
    flex-direction: column;
    padding: 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    padding: 40px 37px 15px 37px;
  }

  &__item {
    display: flex;
    margin-bottom: 45px;
  }

  &__inp {
    border: none;
    outline: none;
    border-bottom: 1px solid #9fa3a7;
    display: flex;
    width: 100%;
    font-size: 14px;
    padding-bottom: 5px;
    color: #2c2c2c;
  }

  &__label {
    display: flex;
    flex: 0 0 100px;
    margin-top: 4px;
    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    color: #9fa3a7;
    background: #fff;
    position: relative;
    z-index: 6;
    font-weight: 700;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__text {
    font-size: 12px;
    color: #9fa3a7;

    span {
      color: #f9593a;
      margin-right: 10px;
      font-size: 16px;
      font-weight: 700;
    }
  }

  &__btn {
    width: 160px;
    height: 45px;
    background: #009fe3;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 14px;
    color: #fff;
    border: none;
    transition: .3s;

    &:hover {
      background: #0086bf;
    }
  }

  &__ps {
    color: #9fa3a7;
    font-size: 10px;
    margin-top: 25px;
  }

  &__item-txtar {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 33%;
      left: 0;
      background: #9fa3a7;
      width: 100%;
      height: 1px;
      z-index: 6;
    }
    &:after {
      content: '';
      position: absolute;
      width: 100%;
      background: #9fa3a7;
      height: 1px;
      top: 66%;
      z-index: 5;
      left: 0;
    }
  }
  &__textarea {
    width: 100%;
    height: 105px;
    border: none;
    outline: none;
    border-bottom: 1px solid #9fa3a7;
    position: relative;
    color: #2c2c2c;
    z-index: 5;
    font-size: 15px;
    line-height: 16px;
    resize: none;
  }

  .req {
    position: relative;
    &:After {
      content: '✔';
      position: absolute;
      top: 50%;
      right: 5px;
      color: #1cc933;
      font-size: 16px;
      transform: translate(-50%, -50%);
      font-weight: 700;
      display: none;
    }

    &:before {
      content: '*';
      position: absolute;
      color: #f9593a;
      font-size: 16px;
      right: 10px;
      top: 50%;
      font-weight: 700;
      transform: translate(-50%, -50%);
    }
  }

}
