@import "~bootstrap/scss/bootstrap-grid";
@import "~magnific-popup/dist/magnific-popup.css";




.header {
  background-color: #fff;
  padding: 30px 0;
  position: fixed;
  width: 100%;

  top: 0;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.05);
  z-index: 50;
}

.logot {
  position: relative;
  z-index: 100;
}

.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  background: #fff;
  margin: 0;
  padding: 0;

  @include media-breakpoint-down(md) {
    display: none;
  }
}

.header__menuItem {
  a {
    display: block;
    color: #184494;
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    text-decoration: none;
    position: relative;
    width: 100%;
    text-align: left;
    padding: 16px 0;

    &:after {
      content: "";
      display: none;
      height: 8px;
      width: 23px;
      background-color: #009fe3;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }

  &_active {
    a {
      &:after {
        display: block;
      }
    }
  }
}

.header__communicationPanel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;

  @include media-breakpoint-down(md) {
    display: none;
  }
}

.header__communicationPanelSocial {
  margin-left: 18px;
  cursor: pointer;

  img {
    width: 31px;
    height: 31px;
  }
}

.header__communicationPanelButton {
  margin-left: 22px;
  border-radius: 3px;
  background-color: #009fe3;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(255, 255, 255);
  border: none;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  text-decoration: none;
  font-weight: bold;
  line-height: 1.2;
  font-size: 14px;
  width: 160px;
  height: 44px;
  cursor: pointer;
}


.header__communicationPanelPhone {
  font-size: 14px;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
  text-decoration: none;
}

.header__buttonMenu {
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  float: right;
  position: relative;
  z-index: 120;

  img {
    height: 40px;
  }

  @include media-breakpoint-up(lg) {
    display: none;
  }
}

.header__rowup {
  background: #fff;
}

.header__menuMobile {
  position: fixed;
  display: none;
  background: #fff;
  overflow: auto;
  top: 105px;
  left: 0;
  right: 0;
  z-index: 80;
  bottom: 0;
  padding: 0 0 30px 0;

  &_show {
    display: block;
  }

  h3 {
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
  }

  @include media-breakpoint-up(lg) {
    display: none;
  }
}

.header__menuMobileNav {
  list-style: none;
  padding: 0 0 30px;
  margin: 0;
  background: #fff;
}

.header__menuMobileCommunicationPanel {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 30px;
  background: #fff;
}

.header__menuMobileCommunicationPanelPhone {
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 20px;
  display: inline-block;
}

.header__menuMobileCommunicationPanelButton {
  border-radius: 3px;
  border: none;
  background: #009fe3;
  padding: 17px 20px;
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  color: rgb(255, 255, 255);
  font-weight: bold;
  margin-bottom: 20px;
}


// Popup
.pop-call {
  position: fixed;
  max-width: 500px;
  margin: 0 auto;
  background: #Fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pop {
  &__block {
    display: flex;
    flex-direction: column;
    padding: 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    padding: 40px 37px 15px 37px;
  }

  &__item {
    display: flex;
    margin-bottom: 45px;
  }

  &__inp {
    border: none;
    outline: none;
    border-bottom: 1px solid #9fa3a7;
    display: flex;
    width: 100%;
    font-size: 14px;
    padding-bottom: 5px;
    color: #2c2c2c;
  }

  &__label {
    display: flex;
    flex: 0 0 100px;
    margin-top: 4px;
    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    color: #9fa3a7;
    background: #fff;
    position: relative;
    z-index: 6;
    font-weight: 700;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__text {
    font-size: 12px;
    color: #9fa3a7;

    span {
      color: #f9593a;
      margin-right: 10px;
      font-size: 16px;
      font-weight: 700;
    }
  }

  &__btn {
    width: 160px;
    height: 45px;
    background: #009fe3;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 14px;
    color: #fff;
    border: none;
    transition: .3s;

    &:hover {
      background: #0086bf;
    }
  }

  &__ps {
    color: #9fa3a7;
    font-size: 10px;
    margin-top: 25px;
  }

  &__item-txtar {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 33%;
      left: 0;
      background: #9fa3a7;
      width: 100%;
      height: 1px;
      z-index: 6;
    }

    &:after {
      content: '';
      position: absolute;
      width: 100%;
      background: #9fa3a7;
      height: 1px;
      top: 66%;
      z-index: 5;
      left: 0;
    }
  }

  &__textarea {
    width: 100%;
    height: 105px;
    border: none;
    outline: none;
    border-bottom: 1px solid #9fa3a7;
    position: relative;
    color: #2c2c2c;
    z-index: 5;
    font-size: 15px;
    line-height: 16px;
    resize: none;

  }

  .req {
    position: relative;

    &:After {
      content: '✔';
      position: absolute;
      top: 50%;
      right: 5px;
      color: #1cc933;
      font-size: 16px;
      transform: translate(-50%, -50%);
      font-weight: 700;
      display: none;
    }

    &:before {
      content: '*';
      position: absolute;
      color: #f9593a;
      font-size: 16px;
      right: 10px;
      top: 50%;
      font-weight: 700;
      transform: translate(-50%, -50%);
    }
  }

}