.pop-call {
  position: fixed;
  max-width: 500px;
  margin: 0 auto;
  background: #Fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}


.popkatalog {
  &__title {
    font-size: 18px;
    color: #009fe3;
    margin: 0 0 60px 0;
  }

  &__block {
    display: flex;
    flex-direction: column;
    padding: 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    padding: 40px 37px 15px 37px;
  }

  &__item {
    display: flex;
    margin-bottom: 45px;
  }

  &__inp {
    border: none;
    outline: none;
    border-bottom: 1px solid #9fa3a7;
    display: flex;
    width: 100%;
    font-size: 14px;
    padding-bottom: 5px;
    color: #2c2c2c;
  }

  &__label {
    display: flex;
    flex: 0 0 100px;
    margin-top: 4px;
    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    color: #9fa3a7;
    background: #fff;
    position: relative;
    z-index: 6;
    font-weight: 700;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__text {
    font-size: 12px;
    color: #9fa3a7;

    span {
      color: #f9593a;
      margin-right: 10px;
      font-weight: 700;
      font-size: 16px;
    }
  }

  &__btn {
    width: 160px;
    height: 45px;
    background: #009fe3;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 14px;
    color: #fff;
    border: none;
    transition: .3s;

    &:hover {
      background: #0086bf;
    }
  }

  &__ps {
    color: #9fa3a7;
    font-size: 10px;
    margin-top: 25px;
  }

  &__item-txtar {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 33%;
      left: 0;
      background: #9fa3a7;
      width: 100%;
      height: 1px;
      z-index: 6;
    }

    &:after {
      content: '';
      position: absolute;
      width: 100%;
      background: #9fa3a7;
      height: 1px;
      top: 66%;
      z-index: 5;
      left: 0;
    }
  }

  &__textarea {
    width: 100%;
    height: 105px;
    border: none;
    outline: none;
    border-bottom: 1px solid #9fa3a7;
    position: relative;
    color: #2c2c2c;
    z-index: 5;
    font-size: 15px;
    line-height: 16px;
    resize: none;

  }

  &__item-name {
    position: relative;


  }

  .req-katal {
    position: relative;

    &:After {
      content: '✔';
      position: absolute;
      top: 50%;
      right: 5px;
      color: #1cc933;
      font-size: 16px;
      transform: translate(-50%, -50%);
      font-weight: 700;
      display: none;
    }

    &:before {
      content: '*';
      position: absolute;
      color: #f9593a;
      font-size: 16px;
      right: 10px;
      top: 50%;
      font-weight: 700;
      transform: translate(-50%, -50%);
    }
  }

  &__item-phone {
    position: relative;


  }
}