@import "~bootstrap/scss/bootstrap-grid";

.wrapper{
  max-width: 100%;
  height: 100%;
  min-width: 420px;
  overflow-x: hidden;
}
.compare__path {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 30px 0;

  a {
    font-size: 12px;
    color: rgb(33, 33, 33);
    line-height: 2.333;
    text-decoration: none;

    &:after {
      content: "|";
      display: inline-block;
      margin: 0 13px;
    }

    &:last-child {
      font-weight: bold;

      &:after {
        display: none;
      }
    }
  }
}

.compare {
  margin-top: 130px;
  font-family: 'Open Sans' ,sans-serif;
  margin-bottom: 40px;
  .container{
    overflow: hidden;
  }
  li,ul{
    list-style-type: none
  }
  &__path {}

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__title {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 40px;
    color: #184494;
    flex-grow: 1;
    margin: 0;
  }

  &__label {
    font-size: 14px;
    position: relative;
    padding-right: 60px ;
    user-select: none;
    &:before{
      content: '';
      background: #ececec;
      border-radius: 10px;
      width: 40px;
      height: 20px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: .25s;
      cursor: pointer;
    }
    &:After{
      content: '';
      position: absolute;
      right: 20px;
      cursor: pointer;
      top: 50%;
      border-radius: 50%;
      width: 16px;
      height: 16px;
      background: #009fe3;
      transition: .25s;
      transform: translateY(-50%);
    }
  }

  &__checkbox {
    visibility: hidden;
    max-height: 0;
    max-width: 0;
    opacity: 0;
    pointer-events: none;
    position: absolute;
  }

  &__inner {
    display: flex;
    margin-top: 30px;
    min-height: 590px ;
  }

  &__list {
    flex: 0 0 320px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 0;
  }
  &__txt{
    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    font-weight: 700;
    color: #9fa3a7;
    border-bottom: 2px solid #dcdde1;
    padding-bottom: 13px;
    display: block;

  }
  &__item {
    display: block;    
    position: relative;
  }

  &__dip {    
    max-height: 0;
    padding: 10px 0;
    overflow-y: hidden;
    transition: 1.3s , border 0s;
    border-bottom: 2px solid #dcdde1;
    
    padding: 0;
    li:nth-child(2n){
      background: #ebebeb;
    }
  }

  &__dip-item {
    font-size: 13px;
    color: #212121;
    padding: 5px;
    margin-left: -5px;
    
  }

  &__diprigth {   
    display: flex;    
    transition: .4s ease-in-out;    
    flex-direction: column;
    flex-grow: 1;
    justify-content: flex-end;
    margin-bottom: 32px;
  }


  &__diprigth-list {
    min-width: 185px;
    max-width: 185px;    
    margin-right: 17px;
    padding: 0;
    opacity: 0;
    transition: .3s;
    transition-delay: .45s;
    margin: 0;
    li:nth-child(2n){
      background: #ebebeb;
    }
  }
  .owl-stage-outer{
    height: 100%;
  }
  .owl-item{
    min-height: 580px;
    position: relative;
  }
  &__heftez{
    position: absolute;
    bottom: 24px;  
    
         
  }
  &__volume{
    position: absolute;
    bottom: 79px;     
    
  }
  &__proportions{
    position: absolute;
    bottom: 135px;
    
    
  }
  
  &__owl-carusel{
    display: flex;
    overflow-x: scroll;
    margin-left: 20px;
  }
  &__diprigth-item {
    font-size: 13px;
    color: #212121;
    padding: 5px 5px 5px 8px;
   
  }


  &__block {
    display: flex;
    margin-left: 20px;
    overflow-x: auto;
  }
  &__mintitle{
    position: relative;
    font-weight: 700;
    font-size: 15px;
    background: #fff;
    color: #212121;
    cursor: pointer;
    padding: 17px 20px 17px 0;
    display: block;
    &:before{
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      border: 4px solid transparent; /* Прозрачные границы */
      border-top: 4px solid #212121; /* Добавляем треугольник */
      margin-top: -2px;
    }
  }

  &__point {
    margin-right: 16px;
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
   
  }

  &__image {
    display: flex;
    width: 185px;
    height: 140px;
    border-radius: 6px;
    background: #f5f5f5;
    position: relative;
    img{
      width: 100%;
      object-fit: contain;
    }
  }
  &__shapes {
    margin-top: 10px;
  }
  &__name {
    font-size: 14px;
    font-weight: 700;
    margin: 15px 0 0 0;
    color: #184494;
    text-decoration: none;
    display: block;
  }
  &__remove{
    position: absolute;
    top: 5px;
    right: 5px;
    width: 10px;
    z-index: 2;
    height: 14px; 
    border: 1px solid #9c9c9c;
    border-radius: 50%;
    cursor: pointer;
    transform: rotate(45deg);
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 7px;
      height: 2px;
      background: #9c9c9c;
      transform: translate(-50%,-50%);
      pointer-events: none;
    }
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 7px;
      height: 2px;
      background: #9c9c9c;
      transform: translate(-50%,-50%)  rotate(90deg);
      pointer-events: none;
    }

  }
}

.compare__owl-carusel::-webkit-scrollbar {
  height: 8px;
}

.compare__owl-carusel::-webkit-scrollbar-thumb {
  background: #bfbfbf;
  border-radius: 15px;
}




.checkb {
  position: absolute;
  visibility: hidden;
  z-index: -22;

}
.compare__checkbox:checked + .compare__labe{
  border-bottom: none;
}
.compare__checkbox:checked + .compare__label:before{
  background: #009fe3;
}
.compare__checkbox:checked + .compare__label:after{
  right: 3px;
  background: #fff;
}


.checkb:checked + .compare__dip{
  max-height: 10rem;
  opacity: 1;
  padding-bottom: 15px;
}
.checkb:checked ~ .compare__diprigth{
  opacity: 1;
  visibility: visible;
}
.active-diprigth{
  opacity: 1;
}

.compare-active{
  opacity: 1;
}

@media (max-width:767px){
  .compare__inner {    
    min-height: 470px;
  }
  .compare__list {
    flex: 0 0 232px
  }

  .compare__dip-item{
    font-size: 11px;
    padding: 5px 0;
    margin-left: 0;
  }
  .compare__owl-carusel{
    margin-left: 10px;
    
  }
  .compare__diprigth-item{
    font-size: 11px;
  }
  .compare__label{
    font-size: 12px;
    text-align: right;
  }
}
@media (max-width: 560px){
  .compare__dip-item {
    min-height: 50px;
    display: flex;
    align-items: center;
  }
  .compare__list {
      flex: 0 0 175px;
  }
  .compare__dip-item{ 
    padding: 5px;  
  }
  .compare__diprigth-item {
      min-height: 50px;
      display: flex;
      align-items: center;
  }
  .compare__inner {
      min-height: 580px;
  }
  .compare__dip-item, .compare__diprigth-item{
    font-size:13px;
  }
  .checkb:checked + .compare__dip {
    max-height: 17rem;
    opacity: 1;
    padding-bottom: 15px;
  }
  .compare__proportions {
   
    bottom: 155px;
  }
  .compare__label{
    padding-right: 45px;
  }
  .compare__title {
    font-size: 35px;
  }
}

