body {
  font-family: $font-stack;
  font-size: 14px;
  text-align: left;
  color: #212121;
  margin: 0;
  background-color: #fff;
  overflow-x: hidden;
  position: relative;
}

button {
  cursor: pointer;

  &:focus,
  &:active {
    outline: none;
  }
}